<script>
import $ from "jquery";
import ProductImg1 from "../../../assets/img/ProductImages/ProductDetail/ProductImg1.jpg";
import ProductImg2 from "../../../assets/img/ProductImages/ProductDetail/ProductImg2.jpg";
