export default {
  // name:'',
  data() {
    return {

    colorx:"#4a5153",
      popupActivo5:false,
  active:false,


 colorx:'#103767',
      select1:3,
      select2:'primary',
      options1:[
        {text: 'Square', value: 1},
        {text: 'Rectangle', value: 2},
        {text: 'Rombo', value: 3},
        {text: 'Romboid', value: 4},
        {text: 'Trapeze', value: 5},
        {text: 'Triangle', value: 6},
        {text: 'Polygon', value: 7},
        {text: 'Regular polygon', value: 8},
        {text: 'Circumference', value: 9},
        {text: 'Circle', value: 10},
        {text: 'Circular sector', value: 11},
        {text: 'Circular trapeze', value: 12},
      ],
      options2:[
        {text: 'Primary', value: 'primary'},
        {text: 'Success', value: 'success'},
        {text: 'Danger', value: 'danger'},
        {text: 'Warning', value: 'warning'},
        {text: 'Dark', value: 'dark'},
      ],








      productId: "",
      qty:1,
      feature:"",
      // 這裡
      Product: {},
      ProductCategory: [],
      ProductImages: {
        ProductImg1: ProductImg1,
        ProductImg2: ProductImg2,
        ProductImg3: ProductImg3,
        ProductImg4: ProductImg4,
      },

      total: 0,

      getNowPage: "Capo",

      PickShow: false,
      GString: false,
      // number1:21,
     product: [],
      isShow: false,
      optionsPick: [
        { text: "0.5mm-紅色", value: "0.5mm-紅色" },
        { text: "0.6mm-橘色", value: "0.6mm-橘色" },
        { text: "0.88mm-綠色", value: "0.88mm-綠色" },
        { text: "1.0mm-藍色", value: "1.0mm-藍色" },
        { text: "1.14mm-紫色", value: "1.14mm-紫色" },
      ],
      optionsStrings: [
        { text: "11002(9~42)", value: "11002(9~42)" },
        { text: "11000(10~50)", value: "11000(10~50)" },

        { text: "11027(11~52)", value: "11027(11~52)" },

        { text: "11052(12~54)", value: "11052(12~54)" },
      ],

    };
  },

  methods: {


    getParam() {
      if (this.$route.params.productId) {
        this.productId = this.$route.params.productId;
        this.qty = this.$route.query.qty;
        if (this.$route.query.feature){
          this.feature = this.$route.query.feature;
        }
      } 

      console.log(this.productId, this.qty,this.feature)
      this.getProduct(this.productId, this.qty,this.feature)

    },

getProduct(id,qty,feature){
     const vm = this;
      const url = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/product/${vm.productId}`;
      this.$http.get(url).then((response) => {
        if (response.data.success) {
          vm.product = response.data.product;
          qty= vm.product.qty
      if (typeof qty !== "number") {
    
    parseInt( vm.product.qty);
        parseInt( qty);
      } else {
        // vm.$store.dispatch("addToCart", { id, qty, feature, num });
        // $("#quickViewModal").modal("hide");
        // vm.$bus.$emit("message:push", "已加入購物車", "success");
          // vm.product.qty=qty
      }
        
           vm.product.feature=feature

           console.log(vm.product)
        }

                vm.ProductCategory = vm.product.category.split(",");

        if (vm.ProductCategory[1] == "GString") {
          vm.GString = true;
        } else {
          vm.GString = false;
        }
        if (vm.ProductCategory[1] == "Pick") {
          vm.PickShow = true;
        } else {
          vm.PickShow = false;
        }
      });
},

    onClickSubCategory(id1, id2) {
      const vm = this;

      vm.getNowPage = id2;
      let genderStr = `/${id1}`;
      vm.$router
        .push({ path: genderStr, query: { id: id2 } })
        .catch((err) => {});
    },

    // 抓單一產品細節 麵包屑

    addToCart(id, qty = 1, feature, num = 1) {
      const vm = this;


           console.log(feature)
  if (vm.GString||vm.PickShow ) {
     if (feature == null) {
        vm.$bus.$emit("message:push", "請選擇款式", "danger");
      } else {
        vm.$store.dispatch("addToCart", { id, qty, feature, num });

        vm.$bus.$emit("message:push", "已加入購物車", "success");
      
      }
  } else{

      vm.$store.dispatch("addToCart", { id, qty, feature, num });

        vm.$bus.$emit("message:push", "已加入購物車", "success");


  }

 
    },

  },
  created() {
    this.getParam()
    this.getProduct();
   $("html,body").animate({ scrollTop: 0 }, 0);
  },
};
</script>
